import React from 'react';



class Table extends React.Component {
    render() {
        const {cdata} = this.props;
        return (
            <table>
                <TableHeader />
                <TableBody d={cdata} />
            </table>
        );
    }
}

const TableHeader = () => {
    return (
        <thead>
            <tr>
                <th>Name</th>
                <th>Job</th>
            </tr>
        </thead>);
}
const TableBody = props => {
    const rows = props.d.map((row, index) => {
        return (
            <tr key={index}>
                <td>{row.name}</td>
                <td>{row.job}</td>
            </tr>
        );
    });
    return (
        <tbody>
            {rows}
        </tbody>);
}

export default Table;